<template>
    <div class="scroll-nav">
        <div class="nav-bar-wrap" :style="{height: navHeight, width: navWidth}">
           <ul
                class="nav-bar"
                :class="{'is-fixed': navBarFixed}"
                :style="{
                    top: navTop,
                    right: navRight,
                    left: navLeft,
                    bottom: navBottom,
                    width: navBarFixed ? navFixedWidth : navWidth,
                    height: navBarFixed ? navFixedHeight : '100%'
                }">
                <li
                    v-for="(item, index) in navMenu"
                    :key="item.value"
                    :style="{color: getNavColor(item), borderBottomColor: getNavColor(item, 'border')}"
                    @click="selectNav(item, index)">
                    {{item.label}}
                </li>
            </ul>
       </div>
       <slot></slot>
    </div>
</template>

<script>
export default {
    name: 'ScrollNav',
    props: {
        // å¯¼èªæ éé¡¹
        menu: {
            required: true,
            type: Array,
            default () {
                // ä¼ å¼æ ¼å¼ï¼{label: '', checked: false}
                return [];
            }
        },
        // æªåºå®åçå¯¼èªæ é«åº¦
        height: {
            type: [Number, String],
            default: 52
        },
        // æªåºå®åçå¯¼èªæ å®½åº¦
        width: {
            type: [Number, String],
            default: '100%'
        },
        // æ»å¨æ¡æå¨åºåçåå­ï¼å¯ç±»åidåç­åç´ æ è¯ï¼
        relativeName: {
            type: String,
            default: 'html'
        },
        // å¯¹æ»å¨æ¡æ»å¨çé«åº¦åè¿ç®çå¼ï¼ç±äºæ»å¨åºåçoffsetParentå¯è½è·å¯¼èªæ çoffsetParentä¸ä¸æ ·ï¼ä¼å­å¨å·®å¼
        // æä»¥å¤æ­å¯¼èªæ æ¯å¦è¾¾å°åºå®æ¡ä»¶æ¯ï¼éè¦å¯¹æ»å¨åºåçscrollTopç»åè¯¥å¼åè¿ç®
        extraFixed: {
            type: Number,
            default: 0
        },
        // ç¨æ¥å¯¹æ»å¨æ¶åªä¸ªå¯¼èªéè¦è¢«æ¿æ´»ï¼åé¢å¤çåå·®å¼è¿ç®å¤æ­
        // å¨ä¸å¸é¡¶çæåµä¸ï¼å¯¼èªæå®çåå®¹åªè¦æ»å¨å°æ»å¨å®¹å¨é¡¶é¨å°±ç®å°äºè¯¥åå®¹äºçå¯¼èªäºï¼å³æ»å¨äºãåå®¹çoffsetTopãçè·ç¦»
        // ä½æ¯å¸é¡¶ä¹åï¼åªè¦æ»å¨å°å¸é¡¶å¯¼èªæ åºé¨å°±ç®å°äºæå®å¯¼èªåå®¹äºï¼æä»¥ç¸å½äºåªè¦æ»å¨ãåå®¹çoffsetTop - å¸é¡¶å¯¼èªæ çé«åº¦ãçè·ç¦»å°±ä¼å°è¾¾ä¸´çå¼
        // è½¬æ¢æå¬å¼æ¥çè§£ï¼cä»£è¡¨å¯¼èªåå®¹çoffsetTopï¼sä»£è¡¨æ»å¨çè·ç¦»ï¼å¸é¡¶å¯¼èªæ é«åº¦ä¸ºhãåªè¦æ»å¨è·ç¦»å¤§äºç­äºä¸é¢è¯´çä¸´çå¼ï¼å³è¯å®å°è¾¾äºå¯¹åºå¯¼èªã
        // å æ­¤å¬å¼ä¸ºï¼ s >= c - hï¼ å³ s + h >= c æ¶ï¼å°è¾¾æ¡ä»¶æç«ï¼å æ­¤æ»å¨å®¹å¨çscrollTopé½è¦å ä¸hï¼ææ¯æ¿æ¥å¤æ­çå¼
        // å½ç¶ä¸é¢è¯´çåªæ¯ä¸è¬çæåµï¼è¿ææ»å¨å®¹å¨ä¸æ¯å¯¼èªå¯¹åºåå®¹çoffsetParentçæåµï¼åéè¦å åãå¶ä»åå·®å¼ãï¼å¦paddingç­ï¼
        // æä»¥è¿éçextraScrollå°±æ¯è¡¨ç¤ºè¿äºãå¶ä»åå·®å¼ãï¼ä¸è¬æåµæ¯è·ä¸é¢çextraFixedç­å¼çï¼è¿éææ²¡ææ²¿ç¨extraFixedæ¯å ä¸ºï¼
        // æä¾æ´å¤å¯è½æ§ç»ç¨æ·ï¼æäºç¨æ·å¯è½ä¸æ°å¥½å°±æ¯ç¨ä¸´çæåµæ¥åºååå®¹è½å°åªä¸ªå¯¼èªä¸ï¼æä»¥æä¾è¯¥å¼ï¼è®©å¯ä»¥æ ¹æ®å®éæåµè¿è¡ä¼ å¼ã
        // ä½æ¯ä¸è®¾ç½®è¯¥å¼æ¶ï¼ä¼é»è®¤æ²¿ç¨extraFixedçå¼
        extraScroll: {
            type: Number,
            default: 0
        },
        needFixed: { // æ¯å¦éè¦åºå®å¯¼èªæ ï¼å¸é¡¶ï¼
            type: Boolean,
            default: true
        },
        top: { // åºå®ä¹åçtopå¼
            type: [Number, String],
            default: 0
        },
        right: { // åºå®ä¹åçrightå¼
            type: [Number, String],
            default: 'auto'
        },
        left: { // åºå®ä¹åçleftå¼
            type: [Number, String],
            default: 'auto'
        },
        bottom: { // åºå®ä¹åçbottomå¼
            type: [Number, String],
            default: 'auto'
        },
        fixedWidth: { // å¯¼èªæ åºå®åçå®½åº¦
            type: [Number, String],
            default: 0
        },
        fixedHeight: { // å¯¼èªæ åºå®åçé«åº¦
            type: [Number, String],
            default: 0
        },
        color: { // å¯¼èªè¢«æ¿æ´»çé¢è²
            type: String,
            default: '#387af6'
        }
    },
    data () {
        return {
            offsetTops: { // è®°å½å¯¼èªæ åå¯¼èªåå®¹çoffsetTop
                navBar: 0
            },
            navBarFixed: false, // å¯¼èªæ æ¯å¦è¢«åºå®äº
            scrollContainer: null, // æ»å¨æ¡æå¨å®¹å¨
            fixedHeightPx: 0 // å¯¼èªæ åºå®åçå®éé«åº¦pxå¼
        };
    },
    computed: {
        // å¦æç¨æ·å¯¼èªæ éé¡¹æªä¼ checkedï¼é£ä¹é»è®¤æcheckedç½®ä¸ºfalse
        navMenu () {
            return this.menu.map(item => {
                this.$set(item, 'checked', item.checked || false);
                return item;
            });
        },
        // ä¸»è¦æ¯æªåºå®åçå¯¼èªæ çé«åº¦
        navHeight () {
            return this.createValue(this.height);
        },
        // ä¸»è¦æ¯æªåºå®åçå¯¼èªæ çå®½åº¦
        navWidth () {
            return this.createValue(this.width);
        },
        // å¯¼èªæ åºå®ä¹åçé«åº¦ï¼å¦ææ²¡æè®¾ç½®æå®é«åº¦ï¼é£ä¹é»è®¤æ¯ç¨æªåºå®åçå¯¼èªæ height
        navFixedHeight () {
            const height = this.fixedHeight ? this.fixedHeight : this.height;
            return this.createValue(height);
        },
        // å¯¼èªæ åºå®ä¹åçå®½åº¦ï¼å¦ææ²¡æè®¾ç½®æå®å®½åº¦ï¼é£ä¹é»è®¤æ¯ç¨æªåºå®åçå¯¼èªæ width
        navFixedWidth () {
            const width = this.fixedWidth ? this.fixedWidth : this.width;
            return this.createValue(width);
        },
        navTop () {
            return this.createValue(this.top);
        },
        navLeft () {
            return this.createValue(this.left);
        },
        navBottom () {
            return this.createValue(this.bottom);
        },
        navRight () {
            return this.createValue(this.right);
        },
        // æ ¹æ®æ¯å¦éè¦å¯¼èªæ åºå®çæ¡ä»¶ä¸ï¼åºåçææ»å¨å¯¼èªçåå·®å¼ï¼å¦ææ²¡è®¾ç½®extraScrollï¼é»è®¤ç¨extraFixed
        scrollDeviation () {
            const extra = this.extraScroll ? this.extraScroll : this.extraFixed
            return this.needFixed ? this.fixedHeightPx + extra : extra;
        },
        // ç»å®æ»å¨äºä»¶çåç´ å¯¹è±¡
        scrollTarget () {
            return this.relativeName.toLowerCase() === 'html' ? window : this.scrollContainer;
        }
    },
    methods: {
        createValue (value) {
            return typeof value === 'number' ? `${value}px` : value;
        },
        getNavColor (nav, type) {
            return nav.checked ? this.color : type === 'border' ? 'transparent' : 'inherit';
        },
        /**
         * éæ©æ é¢è·³å°å¯¹åºåå®¹
         */
        selectNav (nav, index) {
            this.resetNavSelect();
            nav.checked = true;
            this.scrollContainer.scrollTop = this.offsetTops[`content${index}`] - this.scrollDeviation;
        },
        resetNavSelect () {
            this.navMenu.forEach(item => {
                item.checked = false;
            });
        },
        /**
         * çå¬é¡µé¢æ»å¨
         */
        handleScroll () {
            const top = this.scrollContainer.scrollTop;
            // è¿æ¯ç¨æ¥å¤æ­ç®åæ»å¨å¨äºåªä¸ªå¯¼èªä¸çå¼ï¼å¯¹æ»å¨å®¹å¨scrollTopåäºåå·®å¼å¤ç
            const fixedBaseTop = top + this.scrollDeviation;
            const menuLength = this.navMenu.length;
            if (this.needFixed) {
                // è¿æ¯æ§å¶å¯¼èªæ å¸é¡¶ - å¸é¡¶
                if ((top + this.extraFixed) >= this.offsetTops.navBar) {
                    this.navBarFixed = true;
                }
                // è¿æ¯æ§å¶å¯¼èªæ å¸é¡¶ - åæ¶å¸é¡¶
                if ((top + this.extraFixed) < this.offsetTops.navBar) {
                    this.navBarFixed = false;
                }
            }
            this.resetNavSelect();
            // æ»å¨æ¡å°è¾¾åºé¨å°±éä¸­æåä¸ä¸ªå¯¼èª
            if (top + this.scrollContainer.clientHeight >= this.scrollContainer.scrollHeight) {
                this.navMenu[menuLength - 1].checked = true;
                return;
            }
            // ä»¥ä¸é½ä¸ºä¾æ®æ»å¨èªå¨éæ©å¯¹åºå¯¼èª
            for (let i = 0; i < menuLength - 1; i++) {
                if (fixedBaseTop >= this.offsetTops[`content${i}`] && fixedBaseTop < this.offsetTops[`content${i + 1}`]) {
                    this.navMenu[i].checked = true;
                    return;
                }
            }
            if (fixedBaseTop >= this.offsetTops[`content${menuLength - 1}`]) {
                this.navMenu[menuLength - 1].checked = true;
                return;
            }
            this.navMenu[0].checked = true;
        },
        /**
         * è®¡ç®é¡µé¢çåä¸ªoffsetTopï¼ä¸»è¦ä¾ç¶ç»ä»¶ä½¿ç¨ï¼ç¨äºå½ç¶ç»ä»¶åå®¹åçé«åº¦ååæ¶ï¼éæ°è®¡ç®offsetTopå¼
         */
        calcTop (recalNav) {
            this.$nextTick(() => {
                recalNav && (this.offsetTops.navBar = document.querySelector('.scroll-nav .nav-bar-wrap').offsetTop);
                this.navMenu.forEach((item, index) => {
                    this.offsetTops[`content${index}`] = this.$slots.default[index].elm.offsetTop;
                });
            });
        },
        hanldeResize () {
            this.calcTop(true);
        },
        /**
         * è®¡ç®å®éçå¯¼èªæ åºå®åçé«åº¦ï¼å¼æ¯pxæ¶çå¼ã
         * ä¸»è¦æ¯éå¯¹ç¨æ·ä¼ è¿æ¥çé«åº¦æ¯å­ç¬¦ä¸²çå½¢å¼ï¼å¦ç¾åæ¯ç­æåµãå ä¸ºç»ä»¶è¦ç¨å®éçé«åº¦pxå¼æ¥åä¸äºè¿ç®
         */
        calcFixedHeightPx () {
            const div = document.createElement('div');
            div.style.position = 'fixed';
            div.style.height = this.navFixedHeight;
            document.body.appendChild(div);
            this.fixedHeightPx = div.offsetHeight;
            document.body.removeChild(div);
        }
    },
    mounted () {
        this.calcFixedHeightPx();
        this.hanldeResize();
        this.scrollContainer = document.querySelector(this.relativeName);
        this.scrollTarget.addEventListener('scroll', this.handleScroll);
        // å½æµè§å¨å¤§å°åçååæ¶ï¼éæ°è®¡ç®åä¸ªoffsetTop
        window.addEventListener('resize', this.hanldeResize);
    },
    beforeDestroy () {
        this.scrollTarget.removeEventListener('scroll', this.handleScroll);
        window.removeEventListener('resize', this.hanldeResize);
    }
};
</script>

